import {Slider, TYPE, Types} from "../../models/Interfaces";
import {USER_CHOICE_CATEGORY, USER_CHOICE_STYLE, USER_CHOICE_MATERIAL, USER_CHOICE_TYPE} from "../actions/types";

import model_earring from '../../assets/images/model-earring.png';
import model_earrings_identical from '../../assets/images/model-earrings-identical.png';
import model_earrings_two from '../../assets/images/model-earring-two.png';
import model_necklace from '../../assets/images/model-necklace.png';
import model_earring_4sim from '../../assets/images/model-earring-4sim.png';
import model_earrings_identical_4sim from '../../assets/images/model-earrings-identical-4sim.png';
import model_earrings_two_4sim from '../../assets/images/model-earring-two-4sim.png';
import model_necklace_4sim from '../../assets/images/model-necklace-4sim.png';

import earring_yellow from '../../assets/images/img-yellow-gold-earring.png';
import earring_white from '../../assets/images/img-white-gold-earring.png';
import necklace_yellow from '../../assets/images/img-yellow-gold-necklace.png';
import necklace_white from '../../assets/images/img-white-gold-necklace.png';

export interface MainState {
    slides: Slider[];
    userChoice: any;
    price: number;

}

const initState: MainState = {
    slides: [{
        cards: [
            {image: model_earring, simImage: model_earring_4sim, type: TYPE.EARRING, stepsNumber: 999,},
            {
                image: model_necklace,
                simImage: model_necklace_4sim,
                type: TYPE.NECKLACE,
                stepsNumber: 1,
                rightNecklaceClass: "paint-morse-necklace",
                leftNecklaceClass: "",
            }
        ],
        title: 'WHAT\'S YOUR CATEGORY?', screenType: Types.CATEGORY,
        material: {white: necklace_white, yellow: necklace_yellow}
    },
        {
            cards: [
                {
                    image: model_earring,
                    simImage: model_earring_4sim,
                    type: TYPE.SINGLE_EARRING,
                    stepsNumber: 1,
                    rightNecklaceClass: "single-paint-morse",
                    leftNecklaceClass: ""
                },
                {
                    image: model_earrings_identical,
                    simImage: model_earrings_identical_4sim,
                    type: TYPE.IDENTICAL_PAIR,
                    stepsNumber: 1,
                    rightNecklaceClass: "second-paint-morse-right",
                    leftNecklaceClass: "second-paint-morse-left",
                    duplicateMorse: true
                },
                {
                    image: model_earrings_two,
                    simImage: model_earrings_two_4sim,
                    type: TYPE.DISTINCT_PAIR,
                    stepsNumber: 2,
                    rightNecklaceClass: "third-paint-morse-right",
                    leftNecklaceClass: "third-paint-morse-left"
                }],
            title: 'WHAT\'S YOUR STYLE?', screenType: Types.STYLE,
            material: {white: earring_white, yellow: earring_yellow}
        }
        // {cards:[{image:model_necklace,type:TYPE.WHITE_GOLD}
        //     ,{image:model6,type:TYPE.YELLOW_GOLD}],title:'WHAT\'S YOUR MATERIAL?',screenType:Types.MATERTIAL}
    ],
    userChoice: {
        category: '',
        style: '',
        material: '',
        chainDetails: {}
    },
    price: 0
};

const MainReducer = (state: MainState = initState, action: any) => {
    switch (action.type) {
        case USER_CHOICE_CATEGORY:
            return {...state, userChoice: {...state.userChoice, category: action.choice.userChoice}};
        case USER_CHOICE_STYLE:
            return {...state, userChoice: {...state.userChoice, style: action.choice.userChoice}};
        case USER_CHOICE_MATERIAL:
            return {...state, userChoice: {...state.userChoice, material: action.choice.userChoice}};
        case USER_CHOICE_TYPE:
            console.log("action.choice.userChoice  ", action.choice.userChoice);
            return {...state, userChoice: {...state.userChoice, chainDetails: action.choice.userChoice}};
        default:
            return state;
    }

};
export default MainReducer;
