import React from 'react';
import './MaterialComponent.scss';
import {isMobile, isSamsungBrowser} from '../../../assets/Constants';
import {Slider} from '../../../models/Interfaces';

interface Props {

    slider: Slider;
    onClick: Function;
    // style:any;
}

interface State {
    x: number;
    y: number;
    mouseDirection: string;
    text: string;
    hoverWhite: boolean;
    hoverYellow: boolean;
}

export class MaterialComponent extends React.Component<Props, State> {


    constructor(props: Props) {
        super(props);
        this.state = {x: 0, y: 0, mouseDirection: "left", text: "", hoverWhite: false, hoverYellow: false};
    }

    handleMouseMove = (e: any, text: string) => {

        this.setState({
            x: e.clientX,
            y: e.clientY,
            text: text
        });
    };

    render() {
        return (
            <div className="material-page">
                <div className="grid-container first-card-background bb">
                    {!this.state.hoverWhite && !this.state.hoverYellow &&
                    <div className="question"> {'Whats your Material?'.toUpperCase()} </div>}

                    <div className={`mini-item ${this.state.hoverYellow ? "cover" : "mini-item-material"}`}
                         onMouseMove={(e) => this.handleMouseMove(e, "WHITE GOLD")}
                         style={{
                             height: (!isSamsungBrowser && isMobile) ? '78vh' : '100vh',
                             animation: `${!isSamsungBrowser && isMobile ? 'mymove-chrome' : 'mymove'} 1s`,
                         }}
                         onClick={() => this.props.onClick("WHITE GOLD")}
                         onMouseEnter={() => this.setState({hoverYellow: true})}
                         onMouseLeave={() => this.setState({hoverYellow: false})}>
                        <img src={this.props.slider.material.white} className="material-image" alt=""/>

                        {this.state.hoverYellow && isMobile &&
                        <div className="type-mobile-material">
                            {this.state.text}
                        </div>}
                    </div>


                    <div className={`mini-item ${this.state.hoverWhite ? "cover" : "mini-item-material"}`}
                         onMouseMove={(e) => this.handleMouseMove(e, "YELLOW GOLD")}
                         style={{
                             height: (!isSamsungBrowser && isMobile) ? '78vh' : '100vh',
                             animation: `${!isSamsungBrowser && isMobile ? 'mymove-chrome' : 'mymove'} 1s`,
                         }}
                         onClick={() => this.props.onClick("YELLOW GOLD")}
                         onMouseEnter={() => this.setState({hoverWhite: true})}
                         onMouseLeave={() => this.setState({hoverWhite: false})}>
                        <img src={this.props.slider.material.yellow} className="material-image" alt=""/>
                        {this.state.hoverWhite && isMobile &&
                        <div className="type-mobile-material">
                            {this.state.text}
                        </div>}
                    </div>

                    <div className="mouse-movement"
                         style={{
                             top: this.state.y - 6,
                             left: this.state.mouseDirection === "right" ? this.state.x + 25 : this.state.text.length < 13 ? this.state.x - 200 : this.state.x - 295
                         }}>
                        {this.state.text}
                    </div>
                </div>
            </div>
        );

    }
}
