import React from 'react';
import './MorseMenu.scss';
import {ALPHA_TO_MORSE} from '../../../assets/Constants';
import circle from '../../../assets/oval.svg';
import dash from '../../../assets/rectangle.svg';

interface Props {
    onClose: () => void;
    className?: string;
}

interface State {
}

export class MorseMenu extends React.Component<Props, State> {


    constructor(props: Props) {
        super(props);
        this.state = {hover: false};
    }

    componentDidUpdate(prevProps: Props) {
    }


    render() {
        return (
            <>
                <div className={`morse-menu-wrapper ${this.props.className}`}>
                    <div className="close-icon" onClick={() => this.props.onClose()}/>
                    <div className="list-wrapper">
                        <table>
                            <tbody>
                            {Object.entries(ALPHA_TO_MORSE).map(([key, value]) => {
                                if (value) {
                                    let val = value.toString();

                                    return (
                                        <tr className="list-morse" key={key}>
                                            <td>{key}</td>
                                            {
                                                val.split('').map((c: any, index) => {
                                                    const tdKey = `${key}:td:${index}`;
                                                    if (c === '.') {
                                                        return <td className="code" key={tdKey}><img src={`${circle}`}
                                                                                                     className="icon-menu"
                                                                                                     alt=""/>
                                                        </td>;
                                                    } else if (c === '-') {
                                                        return <td className="code" key={tdKey}><img src={`${dash}`}
                                                                                                     className="icon-menu dash-width"
                                                                                                     alt=""/>
                                                        </td>;
                                                    }
                                                    return null;
                                                })
                                            }
                                        </tr>);
                                }
                                return null;
                            })}
                            </tbody>
                        </table>
                    </div>


                    <div className="list-wrapper-mobile">
                        <table className="column-chars">
                            <tbody>
                            {Object.entries(ALPHA_TO_MORSE).slice(0, 13).map(([key, value]) => {
                                if (value) {
                                    let val = value.toString();

                                    return (
                                        <tr className="list-morse" key={key}>
                                            <td>{key}</td>
                                            {
                                                val.split('').map((c: any, index) => {
                                                    const tdKey = `${key}:td:${index}`;
                                                    if (c === '.') {
                                                        return <td className="code" key={tdKey}>
                                                            <img src={`${circle}`} className="icon-menu" alt=""/>
                                                        </td>;
                                                    } else if (c === '-') {
                                                        return <td className="code" key={tdKey}>
                                                            <img src={`${dash}`}
                                                                 className="icon-menu dash-width" alt=""/>
                                                        </td>;
                                                    }
                                                    return null;
                                                })
                                            }
                                        </tr>
                                    );
                                }
                                return null;
                            })}
                            </tbody>
                        </table>
                        <table className="column-chars">
                            <tbody>
                            {Object.entries(ALPHA_TO_MORSE).slice(13, 26).map(([key, value]) => {
                                if (value) {
                                    let val = value.toString();

                                    return (
                                        <tr className="list-morse" key={key}>
                                            <td>{key}</td>
                                            {
                                                val.split('').map((c: any, index) => {
                                                    const tdKey = `${key}:td:${index}`;
                                                    if (c === '.') {
                                                        return <td className="code" key={tdKey}>
                                                            <img src={`${circle}`} className="icon-menu" alt=""/>
                                                        </td>;
                                                    } else if (c === '-') {
                                                        return <td className="code" key={tdKey}>
                                                            <img src={`${dash}`} className="icon-menu dash-width"
                                                                 alt=""/>
                                                        </td>;
                                                    }
                                                    return null;
                                                })
                                            }
                                        </tr>
                                    );
                                }
                                return null;
                            })}
                            </tbody>
                        </table>
                    </div>

                    <div className="bottom-bar-menu">
                        <div className="bottom-bar-menu-item">
                            <svg xmlns="http://www.w3.org/2000/svg" width="7" height="7" viewBox="0 0 8 8">
                                <circle cx="972" cy="383" r="4" fill="#FFF" fillRule="evenodd"
                                        transform="translate(-968 -379)"/>
                            </svg>

                            &nbsp;$560
                        </div>
                        <div className="bottom-bar-menu-item">
                            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="8" viewBox="0 0 20 8">
                                <path fill="#FFF" fillRule="evenodd" d="M978 379H998V387H978z"
                                      transform="translate(-978 -379)"/>
                            </svg>
                            &nbsp;$980
                        </div>
                    </div>
                </div>
                <div className={`full-screen-menu ${this.props.className}`} onClick={() => this.props.onClose()}></div>
            </>
        );

    }
}
